import "leaflet/dist/leaflet.css";
import L, { Map, LatLngExpression } from "leaflet";
import * as d3 from "d3";
import { BaseNode, Node, Intersection, Point, Edge } from "./interfaces";

const _L: any = L;

// eslint-disable-next-line  
delete _L.Icon.Default.prototype._getIconUrl
// eslint-disable-next-line  
_L.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),  
  iconUrl: require('leaflet/dist/images/marker-icon.png'),  
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')  
})

type Listener = (x: any) => Promise<void>;

export class CustomMap {

  map: Map | undefined;
  mainNodes: Node[] = [];
  contId: string = "";

  // Listener to call when svg elements is clicked.
  svgListeners: Listener[] = [];

  // Listener to call when map is clicked.
  leafletListeners: Listener[] = [];
  
  init(contId: string) {
    this.contId = contId;
    const zoomLevel = 17.6;
    this.map = L.map(contId).setView([-10.199165, 123.626700], zoomLevel);
    const urlTemplate = 'https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token={accessToken}';

    // Something wrong with typing
    // It doesn't accept "id".
    // We need to cast to "any".
    const tlOptions: any = {
      attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery  <a href="https://www.mapbox.com/">Mapbox</a>',
      maxZoom: 18,
      id: 'mapbox.streets',
      accessToken: 'pk.eyJ1Ijoiam9tZXRhIiwiYSI6ImNqbTB4eWtqNzB5dzAzcHJ2NGVoaW5wcXAifQ.yXaPLSgJXcn0shC2nYDU0g'
    };

    // Add openstreetmap layer.
    L.tileLayer(urlTemplate, tlOptions).addTo(this.map);

    // Add svg layer
    L.svg().addTo(this.map);

    // Make sure we update each object position when size of map changed.
    this.map.on("moveend", () => this.update());

    // Call each listener in leafletListeners.
    this.map.on("click", (event) => {
      this.leafletListeners.forEach(f => f(event));
    });
  }

  addNodes(nodes: Node[] | Intersection[]) {
    const map: Map = this.map as Map;

    const mainNodes = nodes
      .filter(n => n.jabatan == 'rt' || n.jabatan == 'rw')
      .map(n => (n as Node));

    let listeners = this.svgListeners;

    let selection = d3.select("#" + this.contId)
      .select("svg")
      .selectAll("jalur-d3-circle")
      .data(nodes)
      .enter();

    selection.append("circle")
        .attr("class", "node-circle")
        .attr("cx", n => xLayerPos(map, n))
        .attr("cy", n => yLayerPos(map, n))
        .attr("r", n => 
          n.jabatan == 'intersection' ? 8 : 10
        )
        .style("fill", n => {
          switch (n.jabatan) {
            case 'rt': return '#009688';
            case 'rw': return '#2196F3';
            case 'intersection': return '#D81B60';
            default:
              throw new Error(`Unknown jabatan: ${n.jabatan}`);
          }
        })
        // Make sure this element can trigger an event.
        // .style("pointer-events", "auto")
        // // Change cursor type.
        // .style("cursor", "pointer")
        .attr("stroke-width", 0)
        .attr("fill-opacity", .8)
        .on("click", function(d: any) {
          listeners.forEach(f => f(d));
        });

      d3.select("#" + this.contId)
        .select("svg")
        .selectAll("jalur-d3-circle")
        .data(mainNodes)
        .enter()
        .append("text")
          .attr("x", n => xLayerPos(map, n))
          .attr("y", n => yLayerPos(map, n))
          .text(n => `${n.jabatan.toUpperCase()}-${n.lokasiJabatan}`);
  }

  addEdges(edges: Edge[]) {
    const map: Map = this.map as Map;
    let listeners = this.svgListeners;
    d3.select("#" + this.contId)
      .select("svg")
      .selectAll("jalur-d3-line")
      .data(edges)
      .enter()
      .append("line")
        .attr("class", "jalur-d3-line")
        .attr("x1", n => xLayerPos(map, n.a))
        .attr("y1", n => yLayerPos(map, n.a))
        .attr("x2", n => xLayerPos(map, n.b))
        .attr("y2", n => yLayerPos(map, n.b))
        .attr("stroke-width", 6)
        .attr("stroke", "orange")
        .attr("stroke-opacity", 0.8)
        .on("click", function(d: any) {
          listeners.forEach(f => f(d));
        });;
  }

  addEdgesAnimate(edges: Edge[]) {
    const map: Map = this.map as Map;
    let listeners = this.svgListeners;
    const selection = d3.select("#" + this.contId)
      .select("svg")
      .selectAll("jalur-d3-line");

    edges.map((edge, idx) => {
      // Draw each line at interval 500ms
      const countdown = (idx + 1) * 250;
      setTimeout(() => {
        selection
          .data([ edge ])
          .enter()
          .append("line")
            .attr("class", "jalur-d3-line")
            .attr("x1", n => xLayerPos(map, n.a))
            .attr("y1", n => yLayerPos(map, n.a))
            .attr("x2", n => xLayerPos(map, n.b))
            .attr("y2", n => yLayerPos(map, n.b))
            .attr("stroke-width", 6)
            .attr("stroke", "orange")
            .attr("stroke-opacity", 0.8);
      }, countdown);
    });
  }

  update() {
    const map: Map = this.map as Map;
    d3.selectAll<d3.BaseType, Node>("circle")
      .attr("cx", d => xLayerPos(map, d))
      .attr("cy", d => yLayerPos(map, d));
  }

  addSvgListener(f: (d: any) => Promise<void>) {
    this.svgListeners.push(f);
  }

  addLeafletListener(f: Listener) {
    this.leafletListeners.push(f);
  }

  removeAll() {
    d3.selectAll("circle").remove();
    d3.selectAll("line").remove();
  }

  removeLine() {
    d3.selectAll("line").remove();
  }
}

// Helper functions
function fpos(n: BaseNode) {
  return {
    lat: parseFloat(n.latitude),
    lng: parseFloat(n.longitude)
 };
};

function layerPos(map: Map, node: BaseNode) { 
  let result: any;
  try {
    result = (map as Map).latLngToLayerPoint(fpos(node));
  } catch (err) {
    console.log('HERE');
    console.log(node);
    throw err;
  }
  return result;
}

function xLayerPos(map: Map, node: BaseNode) {
  return layerPos(map, node).x;
}

function yLayerPos(map: Map, node: BaseNode) {
  return layerPos(map, node).y;
}

export function getDistance(origin: number[], destination: number[]) {
  let lon1 = toRadian(origin[1]);
  let lat1 = toRadian(origin[0]);
  let lon2 = toRadian(destination[1]);
  let lat2 = toRadian(destination[0]);

  let deltaLat = lat2 - lat1;
  let deltaLon = lon2 - lon1;
  
  let a = Math.pow(Math.sin(deltaLat / 2), 2) + Math.cos(lat1) * Math.cos(lat2) * Math.pow(Math.sin(deltaLon/2), 2);
  let c = 2 * Math.asin(Math.sqrt(a));
  const EARTH_RADIUS = 6371;
  return c * EARTH_RADIUS * 1000;
}

function toRadian(degree: number) {
  return degree * Math.PI / 180;
}

export function edgeIsConnected(edges: Edge[]) {
  for (let i = 0; i < edges.length - 1; i++) {
    let u = edges[i];
    let v = edges[i + 1];

    let a1 = u.a._id!;
    let b1 = u.b._id!;

    if (a1 == v.a._id! || a1 == v.b._id!) {
      return true;
    }
    if (b1 == v.a._id! || b1 == v.b._id!) {
      return true;
    }
    return false;
  }
}